@font-face {
    font-family: 'SolideMirage-Etroit';
    src: url('/assets/fonts/SolideMirage-Etroit.woff2') format('woff2'),
        url('/assets/fonts/SolideMirage-Etroit.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

:root {
    --font-stack-body: 'acherus_grotesque', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'PingFang TC', sans-serif;
    --font-stack-heading: 'SolideMirage-Etroit', Georgia, 'Times New Roman', Times, 'SongTi TC', serif;
    --font-stack-accent: 'cartograph_cf', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

    --color-1: #fff;
    --color-2: #272A45;
    --color-3: #272A45;
}

section + section {
    margin-top: 1.5em;
}

header, main {
    max-width: 40em;
    margin-right: auto;
    margin-left: auto;
}

h1, h2 {
    font-style: normal;
    text-transform: uppercase;
}

h1 {
    font-size: 3em;
}

h2 {
    font-size: 2em;
    text-align: left;
    margin-bottom: 0.5em;
}

ul {
    margin-top: 0.2em;
}

#contact {
    font-style: italic;

    p + p {
        margin-top: 0;
    }
}

@media (min-width: 500px) {
    #skills-list {
        columns: 2;
        column-gap: 5%;
    }
}

@media (min-width: 600px) {
    h1 {
        font-size: 4em;
    }
    
    h2 {
        font-size: 2em;
    }
}

@media print {
    html {
        color: #000;
    }
    
    body {
        display: block;
    }

    footer {
        display: none;
    }

    header, main {
        max-width: 40rem;
    }

    header {
        margin-top: 0;
        font-size: 1em;
    }
    
    h1, h2, #contact {
        text-align: center;
    }

    h1 {
        font-size: 2.5em;
    }

    a {
        text-decoration: none;
    }

    #contact {
        font-size: 0.8em;
    }
}